import React, {useState,useEffect} from 'react';
import axios from 'axios';
import styled from 'styled-components';
import WeatherCard from '../components/weatherCard';

const apiKey = `d62c3e3ede49b65f11eceeb16ca5c078`;

const WeeklyWeatherContainer = styled.section`
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 600px;
    margin: auto;
`

const WeeklyWeather = () => {
    const [position, setPosition] = useState({lat: 51.5074,lon: 0.1278})
    const [forecast, setForecast] = useState(undefined)
  
  useEffect(() => {
    const success = (data) => {
        setPosition({lat: data.coords.latitude,lon: data.coords.longitude})
    }
    navigator.geolocation.getCurrentPosition(success)
  },[])

  useEffect(() => {
    if(sessionStorage.getItem(`forecast-${position.lat}-${position.lon}`)){
        let sessionData = JSON.parse(sessionStorage.getItem(`forecast-${position.lat}-${position.lon}`))
        setForecast(sessionData)
        console.log('Using Session Storage')
    }else{
      const fetchData = async () => {
        let params = {lon: position.lon, lat: position.lat, appid: apiKey}
        const response = await axios.get('https://api.openweathermap.org/data/2.5/onecall?', {params})
        sessionStorage.setItem(`forecast-${position.lat}-${position.lon}`,JSON.stringify(response.data))
        setForecast(response.data)
        console.log('New Call')
      }
      fetchData()
    }
  },[position])
    console.log(forecast ? forecast.daily : 'loading')
    return (
        <WeeklyWeatherContainer>
            {forecast ? forecast.daily.map((e,i) => (
                <WeatherCard
                    key={i}
                    title={e.weather[0].description}
                    temp={(e.temp.day-275.15).toFixed()}
                    date={e.dt}
                    />
            )) 
            : <p>Loading...</p>}            
        </WeeklyWeatherContainer>
    )
}

export default WeeklyWeather