import React from 'react';
import styled from 'styled-components';

const WeathCardContainer = styled.div`
    width: 200px;
    background: lightblue;
    border-radius: 10px;
    text-align: left;
    padding: 10px;
    h2{
        font-weight: 300;
    }
    p{
        font-weight: 300;
    }
`
const dayMap = ['Sunday','Monday','Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']

const WeatherCard = (props) => {

    const time = new Date(props.date*1000)
    const day =dayMap[time.getDay()]
    const date = time.getDate()
    const suffix =  date === 1 || date === 21 || date === 31 ? `st`
                   : date === 2 || date === 22 ? `nd` 
                   : date === 3 || date === 23 ? `rd` : `th`;


    return (
        <WeathCardContainer>
            <h2>{props.title}</h2>
            <h3>{props.temp} &#x2103;</h3>
            <p>{day} {date}{suffix}</p>
        </WeathCardContainer>
    )
}

export default WeatherCard